			this.getShopList()
			this.getGoodsList()
		},
		
		methods: {
			changTab(arg){
				this.classifyTab = arg;
			},
			getGoodsList(){
				let params = {
					a:0,
					type:1,
					pa:1,
					li:10,
					ob:1,
					orgId:this.$route.params.id?this.$route.params.id:'',
				}
				this.$Api.getGoodsList(params).then((res)=>{
					console.log(res)
				})
