<style lang="scss">
	@import "../../sass/baseSass.scss";
	#shop-goods-list {
		.shop-top{
			position: relative;
			/*padding-top:1.17rem;*/
			height:5.33rem;
			.shop-banner{
				position: relative;
				>img{
					width: 100%;
					height: 5.33rem;
				}
				.shop-footer{
					position: absolute;
					display: flex;
					bottom: 0;
					padding: 0 0.4rem;
					width: 100%;
					height: 1.6rem;
					background: #000;
					.shop-logo{
						position: relative;
    				bottom: 0.67rem;
						width: 1.97rem;
						height: 1.97rem;
						border-radius: 50%;
						border: 1px solid rgb(204, 204, 204);

					}
					.shop-logo-img{
						width: 1.97rem;
						height: 1.97rem;
						border-radius: 50%;
					}
					.shop-text{
						/*flex: 1;*/
						margin-left: 0.4rem;
						display: flex;
						flex-direction:column;
						justify-content:center;

					}
					.shop-name{
						font-size: 0.4rem;
						color: #fff;
					}
					.shop-good{
						font-size: 0.37rem;
						color: #fff;

					}
				}
			}
		}
		.shop-classify{
			display: flex;
			height: 1.07rem;
			line-height: 1.07rem;
			font-size: 0.4rem;
			border-bottom: 1px solid $border-color;
			>div{
				text-align: center;
				flex:1;
			}
			.active{
				color: $label-color;
				border-bottom: 0.05rem solid rgb(222, 127, 239);
			}
		}
		.order-list {
			display: flex;
			font-size: 0.4rem;
			flex-direction: row;
			border-bottom: 1px solid $border-color;
			height: 1.07rem;
			line-height: 1.07rem;
			align-items: center;
			.active {
				color: $label-color;
			}
			>div {
				position: relative;
				height: 0.67rem;
				line-height: 0.67rem;
				text-align: center;
				flex: 1;
				border-right: 1px solid $border-color;
				&:last-child {
					border-right: none;
				}
			}
			.rang-icon-no {
				&:after{
					content: '';
					height: 0.4rem;
					width: 0.2rem;
					display: block;
					position: absolute;
					right:0.53rem;
					top:51%;
					transform: translateY(-50%);
					background-image: url(../../assets/rang-icon-no.png);
					background-repeat: no-repeat;
					background-size: 0.2rem 0.4rem;
				}
			}
			.rang-icon-up {
				&:after{
					content: '';
					height: 0.4rem;
					width: 0.2rem;
					display: block;
					position: absolute;
					right:0.53rem;
					top:51%;
					transform: translateY(-50%);
					background-image: url(../../assets/rang-icon-up.png);
					background-repeat: no-repeat;
					background-size: 0.2rem 0.4rem;
				}
				
			}
			.rang-icon-down {
				&:after{
					content: '';
					height: 0.4rem;
					width: 0.2rem;
					display: block;
					position: absolute;
					right:0.53rem;
					top:51%;
					transform: translateY(-50%);
					background-image: url(../../assets/rang-icon-down.png);
					background-repeat: no-repeat;
					background-size: 0.2rem 0.4rem;
				}
				
			}
		}
	}
</style>


